// Icon font

@font-face {
  font-family: 'entypo-custom';
  src: url('data:application/octet-stream;base64,') format('woff');
}

[class^="icon-"]::before,
[class*=" icon-"]::before,
[class^="icon-after-"]::after,
[class*=" icon-after-"]::after {
  display: inline-block;
  font-family: 'entypo-custom';
  font-style: normal;
  font-variant: normal;
  font-weight: normal;
  padding-left: 0;
  padding-right: 0.5em;
  speak: none;
  text-align: center;
  text-decoration: inherit;
  text-transform: none;
  width: 1em;
}

